html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: 'Helvetica Neue', Helvetica, Arial, 'Lato', Georgia, Palatino, 'Palatino Linotype', Sans-Serif;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

blockquote,
q {
	quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

a {
	color: black;
	padding: 0px 4px;
	text-decoration: none;
	border-bottom: 1px solid #d5d5d5;
	position: relative;
	display: inline-block;
	font-weight: 800;

	transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;

	&:hover {
		color: #000;
		transform: scale(1.05);
		-webkit-transform: scale(1.05);
		cursor: pointer;
	}

	&.noSlide {
		border: none;
	}
}

body {
	background: #f3f7ff;
	border: 0;
	color: #000;
	margin: 0;
	padding: 0;

	&#yellow {
		background-color: #ffff11;
	}

	&#darkGreen {
		background-color: #3e4938;
		color: white;

		a {
			color: white;
		}
	}

	&#lightGrey {
		background-color: #b2baaa;
	}
}

h1 {
	font-size: 2em;
	font-weight: 900;
	line-height: 1.4em;
	margin: 0 0 0.6em 0;
}

h2 {
	font-size: 26px;
	font-weight: 900;
	line-height: 30px;
	margin: 35px 0 10px 0;
}

h3 {
	font-size: 20px;
	margin: 0 0 0.6em;
	font-weight: 900;
}

h4 {
	font-size: 0.666em;
	margin: 0 0 2.25em;
	font-weight: 700;
}

p,
li {
	font-size: 21px;
	line-height: 34px;
	margin: 10px 0 15px 0px;
	font-weight: 400;
}

strong,
b {
	font-weight: 700;
}

em {
	font-style: italic;
}

img {
	border: 0;
}

i {
	font-style: italic;
}

ul {
	margin: 0 0 20px 0;
}

li {
	list-style-type: disc;
	margin: 0 0 25px 50px;
}

hr {
	border: 1px #000 solid;
	border-bottom: 0px;
	border-left: 0px;
	border-right: 0px;
	margin: 50px 0 40px 0;
}

::-moz-selection {
	background: #ffff11;
	color: black;
	text-shadow: none;
}

::selection {
	background: #ffff11;
	color: black;
	text-shadow: none;
}

.left {
	float: left;
}

.right {
	float: right;
}

.last {
	margin-right: 0px !important;
}

.container {
	max-width: 1000px;
	width: 80%;
	position: relative;
	margin: 0 auto;
	padding: 0 0%;
	box-sizing: border-box;
}

blockquote {
	font-size: 18px;
	font-style: italic;
	max-width: 700px;
	margin: 0.25em 0;
	padding: 0.25em 40px;
	line-height: 1.45;
	position: relative;
	color: #383838;

	&:before {
		display: block;
		content: '\201C';
		font-size: 80px;
		position: absolute;
		left: -10px;
		top: -20px;
		color: #7a7a7a;
	}
}

header {
	overflow: hidden;
	top: 0;
	left: 0;
	width: 100%;
	padding: 0;
	margin: 30px auto;
	line-height: 30px;
	text-align: center;
	max-width: 90%;

	a.logo {
		border: none;
		text-decoration: none;
		padding: 0;
		margin: 20px;

		svg {
			width: 170px;
			height: auto;
			max-width: 100%;
		}
	}

	ul {
		margin: 0;

		li {
			display: inline;
			padding: 0;
			margin: 0;
			line-height: 50px;

			&.current {
				border-bottom: 0px solid #000 !important;
				margin-bottom: 0 !important;

				a {
					font-weight: 900;
				}
			}

			a {
				text-align: center;
				letter-spacing: 0px;
				font-weight: 500;
				font-size: 20px;
				text-decoration: none;
				text-transform: none;
				padding: 15px 10px;
				margin: 0 0px;
				transition-property: all;
				transition-duration: 100ms;
				transition-timing-function: ease-in;
				-webkit-transition-property: all;
				-webkit-transition-duration: 100ms;
				-webkit-transition-timing-function: ease-in;
				white-space: nowrap;
				border: 0;
			}
		}
	}
}

article {
	&.container {
		picture,
		img,
		video,
		.superwide,
		iframe,
		.fluid-width-video-wrapper {
			max-width: 100%;
		}

		.banner {
			display: block;
			margin: 40px 0 20px 0;
		}

		.partner {
			text-align: center;
			justify-content: center;

			svg {
				max-width: 100%;
				height: auto;
				width: 100%;
			}
		}

		li {
			&.small {
				font-size: 16px;
				line-height: 28px;
			}
		}
	}
}

.socialGrid svg {
	transition: all 0.2s ease-in-out;
	-webkit-transition: all 0.2s ease-in-out;
	position: relative;
	padding: 0;
	box-sizing: inherit;

	&:hover {
		transform: scale(1.06);
		-webkit-transform: scale(1.06);
		z-index: 400;
		cursor: pointer;
	}
}

.socialGrid svg {
	float: left;
	width: 100%;
}

.lozad {
	opacity: 0;
}

.fade {
	-moz-animation: fadeIn ease-in-out 0.6s;
	-o-animation: fadeIn ease-in-out 0.6s;
	animation: fadeIn ease-in-out 0.6s;
	-webkit-animation-name: fadeIn;
	-webkit-animation-duration: 0.6s;
	-webkit-animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in-out;
	-webkit-animation-fill-mode: forwards;
	opacity: 1 !important;
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@-moz-keyframes fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@-webkit-keyframes fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@-ms-keyframes fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@-o-keyframes fadein {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

footer {
	clear: both;
	margin: 20px auto 40px auto;
	overflow: hidden;
	max-width: 90%;
	text-align: center;

	p {
		font-size: 16px;
		line-height: 28px;
		margin: 10px 0;
		opacity: 0.5;

		span {
			padding: 10px 10px;
		}
	}

	.social {
		margin: 30px 0;

		svg,
		img {
			width: 60px;
			height: 84px;
			margin: 0 1%;
			transition: transform 0.2s ease-in-out;
			-webkit-transition: -webkit-transform 0.2s ease-in-out;

			&:hover {
				transform: scale(1.1);
				-webkit-transform: scale(1.1);
			}
		}
	}

	.beoriginal {
		margin: 30px 0;

		svg {
			width: 220px;
			max-width: 100%;
			height: auto;
		}
	}
}

.button {
	padding: 10px 7px;
	border: 0;
	width: 200px;
	height: 45px;
	color: #fff;
	background-color: #0099ff;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	-khtml-border-radius: 2px;
	border-radius: 2px;
	margin: 2px;
	font-size: 14px;
}

@media (min-width: 800px) {
	.css_onlymobile {
		display: none;
	}
	.css_onlydesktop {
		display: block;
	}
}

@media (max-width: 600px) {
	#main {
		width: auto;
		float: none;
		padding: 20px 10px;
		margin: 0;
	}

	img[width='900'],
	article img {
		overflow: hidden;
	}
}

/* @end */
